import Vue from 'vue'
import Vuex from 'vuex'
import {mockService} from '../http/service'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 弹窗状态
    dialog: false,
    addDialog: false,
    editDialog: false,
    deleteDialog: false,
    tipDialog: false,
    menuList: [] // 模拟数据菜单表
  },
  mutations: {
    dialog: (state, dialog) => {
      state.dialog = dialog
    },
    addDialog: (state, addDialog) => {
      state.addDialog = addDialog
    },
    editDialog: (state, editDialog) => {
      state.editDialog = editDialog
    },
    deleteDialog: (state, deleteDialog) => {
      state.deleteDialog = deleteDialog
    },
    tipDialog: (state, tipDialog) => {
      state.tipDialog = tipDialog
    },
    menuList: (state, menuList) => {
      state.menuList = menuList
    }
  },
  actions: {
    setDialog({commit}, dialog) {
      commit('dialog', dialog)
    },
    setAddDialog({commit}, addDialog) {
      commit('addDialog', addDialog)
    },
    setEditDialog({commit}, editDialog) {
      commit('editDialog', editDialog)
    },
    setDeleteDialog({commit}, deleteDialog) {
      commit('deleteDialog', deleteDialog)
    },
    setTipDialog({commit}, tipDialog) {
      commit('tipDialog', tipDialog)
    },
    setMenuList: ({commit}) => {
      mockService.menuList({
        cb: (res) => {
          console.log(res)
          commit('menuList', res)
        }
      })
    }
  }
})
