export function addSchedulingField(form) {
  console.log('---', form)
  let params = {}
  params.ScheduleNumber = '165587'
  params.ServiceObject = 'Bridge'
  params.AisleCode = form.AisleCode
  params.BridgeCode = form.BridgeCode
  params.OperatorName = form.user.RealName
  params.OperatorID = form.user.UserID
  params.Area = form.Area
  params.Attribute = form.Attribute
  params.StartTime = form.StartTime
  params.StopTime = form.StopTime
  params.SubmitTime = form.SubmitTime
  params.DispatcherID = 0
  return params
}

