<template>
  <div class="login-layout h100">
    <img src="../../assets/img/loginTitle.png" class="login-title">
    <div class="login-container">
      <h2 class="container-title">用户登录</h2>
      <!--登录输入框-->
      <el-form :model="form" :rules="rules" label-width="0" ref="form" class="login-form">
        <el-form-item prop="UserName" class="name-item">
          <i class="el-icon-user-solid"></i>
          <el-input v-model.trim="form.UserName" placeholder="用户名" @keyup.enter.native="submit"
                    class="name"></el-input>
        </el-form-item>
        <el-form-item prop="Password" class="password-item">
          <i class="el-icon-lock"></i>
          <el-input v-model.trim="form.Password" type="password" placeholder="密码" @keyup.enter.native="submit"
                    class="password"></el-input>
        </el-form-item>
      </el-form>
      <!--登录按钮-->
      <div class="login-txt">
        <el-checkbox v-model="isRemember" class="remember fl">记住密码</el-checkbox>
        <span class="forget fr" @click="forgetPassword">忘记密码？</span>
      </div>
      <el-button type="primary" class="login-btn" @click="submit">
        登录
      </el-button>
    </div>
  </div>
</template>
<script>
  import {adminService} from '../../http/service'
  import {toLoginFiled} from '../../http/filed'

  export default {
    data() {
      return {
        form: {},
        rules: {
          UserName: [
            {required: true, message: '请输入用户名'}
          ],
          Password: [
            {required: true, message: '请输入密码'}
          ]
        },
        isRemember: true
      }
    },
    methods: {
      submit() {
        this.$router.push(`/home`)
        this.$refs.form.validate((valid) => {
          // if (valid) this.doLogin()
        })
      },
      doLogin() {
        adminService.toLogin({
          params: toLoginFiled(this.form),
          cb: (res) => {
            sessionStorage.setItem('token', JSON.stringify(res))
            this.$router.push(`/home`)
          }
        })
      },
      forgetPassword() {
      }
    }
  }
</script>
<style lang="less" scoped>
  .login-layout {
    position: relative;
    background: url("../../assets/img/loginBg.png");
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    min-width: 1000px;
    min-height: 550px;

    .login-title {
      position: absolute;
      width: 25%;
      top: 7%;
      left: 10%;
    }

    .login-container {
      position: absolute;
      width: 420px;
      height: 400px;
      background: url("../../assets/img/loginInput.png");
      -webkit-background-size: 100% 100%;
      background-size: 100% 100%;
      top: 26%;
      left: 56%;
      padding: 46px 40px;

      .container-title {
        font-size: 28px;
        color: #fff;
        text-align: center;
        font-weight: normal;
      }

      .login-form {
        margin-top: 40px;

        .name-item, .password-item {
          position: relative;

          i {
            font-size: 24px;
            color: #fff;
            position: absolute;
            left: 16px;
            top: 13px;
          }
        }
      }

      .login-txt {
        height: 60px;
        line-height: 60px;

        .remember {
          color: #fff;

        }

        .forget {
          color: #0099f6;
          font-size: 14px;
        }
      }

      .login-btn {
        width: 100%;
        height: 50px;
        font-size: 20px;
      }
    }
  }

</style>
