<template>
  <div class="container-layout h100">
    <slider :visiblePath="containerConfig.visiblePath">
    </slider>
    <section class="wrapper h100">
      <div class="container-header">
        <div class="time">
          <i class="el-icon-time"></i>
          {{time}}
        </div>
        <div class="user">
          <img src="../../assets/img/userImg.png" class="user-img">
          <div class="user-txt">
            <p class="user-role">运营管理员</p>
            <p class="user-name">luck001</p>
          </div>
        </div>
        <div class="exit">
          <i class="el-icon-switch-button"></i>
          <span class="exit-txt" @click="exit">退出</span>
        </div>
      </div>
      <div class="wrapper-body w100 b0 scroll-style">
        <transition name="fade">
          <router-view></router-view>
        </transition>
      </div>
    </section>
  </div>
</template>
<script>
  import {slider} from './pages'
  import {newTime} from '../../utils/public'

  export default {
    components: {
      slider
    },
    computed: {
      containerConfig() {
        return global.airportConfig.container
      }
    },
    data() {
      return {
        time: ''
      }
    },
    mounted() {
      this.onReady()
    },
    methods: {
      onReady() {
        newTime((time) => {
          this.time = time
        })
      },
      exit() {
        this.$router.push('/')
      }
    }
  }
</script>
<style lang="less" scoped>
  .container-layout {
    display: flex;

    .wrapper {
      position: relative;
      flex: 1;
      display: block;

      .container-header {
        position: relative;
        height: 77px;

        .time {
          font-size: 18px;
          color: #b1b1b1;
          line-height: 76px;
          position: absolute;
          right: 350px;
          top: 0;
        }

        .user {
          position: absolute;
          height: 76px;
          right: 120px;
          top: 0;

          .user-img {
            width: 50px;
            margin-top: 13px;
          }

          .user-txt {
            display: inline-block;
            height: 76px;
            vertical-align: top;
            margin-left: 12px;

            .user-role {
              font-size: 14px;
              margin-top: 19px;
              color: #2e2e2e;
            }

            .user-name {
              font-size: 12px;
              margin-top: 5px;
              color: #cfcfcf;
            }
          }
        }

        .exit {
          position: absolute;
          right: 48px;
          top: 0;
          line-height: 76px;
          color: #b1b1b1;
          font-size: 18px;

          .el-icon-switch-button {
            margin-right: 5px;
          }
        }
      }

      .wrapper-body {
        position: absolute;
        top: 77px;
        background: #eef3f6;
        overflow: auto;
      }
    }
  }
</style>
