import Vue from 'vue'
import App from './App.vue'
import router from './router/index'
import store from './store/index'
import ElementUI from 'element-ui' // 全局引入element
import 'element-ui/lib/theme-chalk/index.css' // 正常样式

import './assets/css/reset.css' //引入基础样式
import './assets/css/set.css' //引入基础样式
Vue.use(ElementUI, {
  size: 'mini' // 改变element尺寸规模
})

Vue.config.productionTip = false

const vm = new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

export default vm // 将vm暴露
