import api from './api'
import request from './request'

const mockService = {
  async menuList({params, cb}) { // 菜单数据
    const res = await request.apiGet(api.mockService.menuList, params)
    cb(res.data)
  }
}

/*系统管理接口*/
const adminService = {
  async toLogin({params, cb}) { // 登录接口 /User/Login
    const res = await request.apiPost(api.adminService.toLogin, params)
    cb(res)
  },
  async getUserList({params, cb}) { // 用户列表 /Sys/UserList
    const res = await request.apiPost(api.adminService.getUserList, params)
    cb(res)
  },
}


/*调度接口*/
const dispatchService = {
  async getDynamic({params, cb}) { // 获取航班动态
    const res = await request.apiPost(api.dispatchService.getDynamic, params)
    cb(res)
  },
  async getDispatch({params, cb}) { // 航班调度数据
    const res = await request.apiPost(api.dispatchService.getDispatch, params)
    cb(res)
  },
  async audit({params, cb}) { // 航班调度审计
    const res = await request.apiPost(api.dispatchService.audit, params)
    cb(res)
  },
  async remark({params, cb}) { // 航班调度标注
    const res = await request.apiPost(api.dispatchService.remark, params)
    cb(res)
  },
  async getScheduling({params, cb}) { // 操作员排班数据
    const res = await request.apiPost(api.dispatchService.getScheduling, params)
    cb(res)
  },
  async addScheduling({params, cb}) { // 添加操作员排班数据
    const res = await request.apiPost(api.dispatchService.addScheduling, params)
    cb(res)
  },
  async getEquipment({params, cb}) { // 设备管理数据
    const res = await request.apiPost(api.dispatchService.getEquipment, params)
    cb(res)
  },
  async getMobileDevices({params, cb}) { // 移动设备管理数据
    const res = await request.apiPost(api.dispatchService.getMobileDevices, params)
    cb(res)
  }
}


export {
  mockService,
  adminService,
  dispatchService
}
