import request from 'axios'
import qs from 'qs'
import helper from './helper'
import {Loading} from 'element-ui'

const JSON_HEADER = {'Content-Type': 'application/json'}
const FILE_HEADER = {'Content-Type': 'multipart/form-data'}

let loadingInstance

const showLoading = () => {
  loadingInstance = Loading.service({
    lock: true,
    text: '玩命加载中...',
    spinner: 'el-icon-loading',
    background: 'rgba(0, 0, 0, 0)'
  })
}

const hideLoading = () => {
  loadingInstance.close()
}

class Request {
  constructor() {
    // 参数
    request.defaults.params = {}
    // 最大请求时间
    request.defaults.timeout = 120 * 1000
    // http code
    request.defaults.validateStatus = status => {
      return status >= 200 && status < 300
    }
    this.instance = request
  }

  getInstance() {
    return this.instance
  }


  apiGet(url, params = {}) {
    showLoading()
    return new Promise((resolve, reject) => {
      this.instance.get(`${url}`, {
        params: {
          ...params
        }
      }).then((res) => {
        hideLoading()
        resolve(res.data)
      }).catch((error) => {
        reject(error)
        hideLoading()
        helper.E()
      })
    })
  }

  apiPost(url, params = {}, header = {}) {
    showLoading()
    let token = JSON.parse(sessionStorage.getItem('token'))
    if (token) {
      header = {"Authorization": `Bearer ${token}`}
    }
    return new Promise((resolve, reject) => {
      this.instance.post(`${url}`, qs.stringify({Data: JSON.stringify(params)}), {headers: header}).then((res) => {
        hideLoading()
        if (res.data.Success) {
          resolve(res.data.Data)
        } else {
          helper.E(res.data.Message)
        }
      }).catch((error) => {
        reject(error)
        hideLoading()
        helper.E()
      })
    })
  }

  apiFile(url, params = {}) {
    showLoading()
    return new Promise((resolve, reject) => {
      this.instance.post(`${url}`, params, {headers: FILE_HEADER}
      ).then((res) => {
        hideLoading()
        resolve(res.data)
      }).catch((error) => {
        reject(error)
        hideLoading()
        helper.E()
      })
    })
  }

  apiJSON(url, params = {}) {
    showLoading()
    return new Promise((resolve, reject) => {
      this.instance.post(`${url}`, params, {headers: JSON_HEADER}
      ).then((res) => {
        hideLoading()
        resolve(res.data)
      }).catch((error) => {
        reject(error)
        hideLoading()
        helper.E()
      })
    })
  }
}

export default new Request()

