<template>
  <div id="app">
    <router-view/>
  </div>
</template>
<script>
  import {mockService} from './http/service'
  import {formatData, formatRouter} from './utils/public'

  export default {
    name: 'App',
    computed: {
      baseConfig() {
        return global.airportConfig.base
      }
    },
    mounted() {
      this.onReady();
      this.routerChange();
    },
    methods: {
      onReady() {
        // 1、设标题
        document.title = this.baseConfig.title
        // 2、设置基础菜单
        this.getMenuList()
        // let token = sessionStorage.getItem('token')
        // if (token) {
        //   if(this.$route.path === '/login'){
        //     this.$router.push('/home')
        //   }
        // } else {
        //   this.$router.push('/')
        // }
      },
      getMenuList() {
        let modules = JSON.parse(sessionStorage.getItem('modules'))
        let menuList = JSON.parse(sessionStorage.getItem('menuList'))
        if (!modules) {
          mockService.menuList({
            cb: (res) => {
              sessionStorage.setItem('menuList', JSON.stringify(res));
              const modules = formatData(res)
              sessionStorage.setItem('modules', JSON.stringify(modules));
              window.location.reload();
            }
          })
        }
        if (menuList) {
          this.$router.addRoutes(formatRouter(menuList))
        }
      },
      routerChange() {
        this.$router.beforeEach((to, from, next) => {
          next()
        })
      }
    }
  }
</script>
