import vm from '../main'
import dateFormat from 'dateformat'


/*
 * 时间格式转换
 * @params time 传入时间毫秒值
 * @params dateLabel 传入时间转换规则
 * */
export function GetDateFormat(time = new Date().getTime(), dateLabel = 'yyyy/mm/dd HH:MM:ss') {
  return dateFormat(time, dateLabel)
}

/*
* 时钟定时器
* */
export function newTime(cb, dateLabel = 'yyyy/mm/dd HH:MM:ss') {
  setInterval(() => {
    let time = dateFormat(new Date().getTime(), dateLabel)
    cb(time)
  }, 1000)
}

/*
 * 格式化路由层级，得到路由解析需要的格式
 * @params modules 路由列表
 * */
export function formatData(data) {
  const modules = []
  data.forEach((item) => {
    if (item.menu_id.substring(3, 6) === '000') modules.push(item)
    item.modules = []
  })
  for (let i = 0; i < modules.length; i++) {
    data.forEach((item) => {
      if (item.menu_id.substring(3, 6) !== '000' &&
        item.menu_id.substring(0, 3) === modules[i].menu_id.substring(0, 3)) {
        modules[i].modules.push(item)
      }
    })
  }
  return modules
}

/*
 * 格式化缓存路由列表 封装成vue-router需要的格式
 * */

export function formatRouter(modules) {
  const routes = [{
    path: '/container',
    component: resolve => require([`../views/container/container.vue`], resolve),
    children: [],
    name: '容器'
  }];
  modules.forEach((item, index) => {
    if (item.isLink) {
      routes[0].children.push({
        path: `/${item.url_address}`,
        component: resolve => require([`../views/${item.url_address}/${item.url_address}.vue`], resolve),
        name: `${item.menu_name}`
      });
    }
  });
  return routes
}
